.footer {
    height: 300px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    background-color: black;

}

.content {
    width: 1200px;
    border: 1px solid black;
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    flex-direction: column;
    background-color: black;
}

.title {
    display: flex;
    justify-content: center;
    background-color: darkkhaki;
    text-align: center;
    margin: 0 auto;
    padding: 7px;
    border-radius: 2px;

}

.acsess {
    display: flex;
    justify-content: center;
    background-color: darkkhaki;
    text-align: center;
    margin: 0 auto;
    padding: 7px;
    border-radius: 2px;

}

.main {
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    border: 1px solid black;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: black;
    flex-wrap: wrap;
    width: 100%;

}

.main div {
    border: 1px solid black;
    width: 100px;
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 10px;
    color: white;
}

@media (max-width: 480px) {
    .main {
        align-items: center;
        flex-direction: column;
    }

}


