.wrapper {
    background-color: black;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.container {
    display: flex;
    justify-content: center;
    width: 1200px;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 0 auto;
}

.projects {
    background-color: lightgoldenrodyellow;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

}


.projects div img {
    height: 400px;
    width: 300px;
}

h2 {
    text-align: center;
    margin-top: 0px;
    color: white;
    position: relative;
}

.line{
    margin: 0 auto;
    height: 1px;
    width: 100px;
    background-color: red;
    margin-bottom: 30px;
}
.navigation {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    flex-wrap: wrap;
}

.navigation a {
    margin-left: 10px;
    font-size: 20px;
    color: white;
}

.photosBlocks {
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 20px;
    color: black;
    font-style: italic;
    font-weight: bold;
    width: 300px;

}
.button{
    width: 100px;
    position: absolute;
    top:50px;
    left: 100px;
    background-color: peachpuff;
    color: black;
    border-radius: 5px;
    opacity: 0.8;
}
.imgButton{
    display: flex;
    flex-direction: column;
    position: relative;
}
.descPhotos{

    background-color: darkmagenta;
    color: white;
}
.photosBlocks span {
    text-align: center;
}

@media (max-width: 480px) {
    .navigation {
        align-items: center;
        flex-direction: column;
    }

}
@media (max-width: 350px) {
    .titleBlock {
       margin-right: 50px;
    }

}
@media (max-width: 290px) {
    .titleBlock {
        margin-right: 100px;
    }
}


@media (max-width: 350px) {
    .projects div img {
        height: 350px;
        width: 250px;
    }
    .button{
        width: 100px;
        position: absolute;
        top:10px;
        left: 75px;
        background-color: peachpuff;
        color: black;
        border-radius: 5px;
    }
    .photosBlocks .descPhotos{
        width: 250px;
    }

}
@media (max-width: 290px) {
    .projects div img {
        height: 300px;
        width: 200px;
    }
    .button{
        width: 100px;
        position: absolute;
        top:0px;
        left: 50px;
        background-color: peachpuff;
        color: black;
        border-radius: 5px;
    }
    .photosBlocks .descPhotos{
        width: 200px;
    }

}




